<template>
    <div class="debug-box">
        <div class="debug-nav-line">
            <div class="route">
                <router-link class="icon-route" to="/debug/base">
                    <IconTextButton :module="module" :permission="1" :iconName="baseIcon" :text="$t('dbgbase.basis')" iconSize="2.5rem"
                        interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line"><span class="line"></span></div>
            <div class="route">
                <router-link class="icon-route" to="/debug/io">
                    <IconTextButton module="DEBUG_IO" :permission="1" :iconName="controlIcon" text="IO" iconSize="2.5rem"
                        interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line"><span class="line"></span></div>
            <div class="route">
                <router-link class="icon-route" to="/debug/totalline">
                    <IconTextButton module="DEBUG_FILEDBUS" :permission="1" :iconName="networkIcon" :text="$t('dbgtln.totalline')"
                        iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
            <div class="vertical-line"><span class="line"></span></div>
            <div class="route">
                <router-link class="icon-route" to="/debug/demarcate">
                    <IconTextButton module="CALIBRATION" :permission="1" :iconName="userIcon" :text="$t('dbgdmrct.demarcate')"
                        iconSize="2.5rem" interval="-0.2rem" fontSize="1.375rem"></IconTextButton>
                </router-link>
            </div>
        </div>
        <div class="debug-content">
            <router-view></router-view>
        </div>
    </div>
</template>
<script>
import IconTextButton from '../components/controls/IconTextButton.vue'

export default {
    components: { IconTextButton },
    data() {
        return { module: "DEBUG" };
    },
    computed: {
        baseIcon() {
            return (this.$route.path.indexOf('debug/base') != -1) ? "ActiveCube" : "StaticCube"
        },
        controlIcon() {
            return (this.$route.path.indexOf('io') != -1) ? "ActiveIO" : "StaticIO"
        },
        networkIcon() {
            return (this.$route.path.indexOf('totalline') != -1) ? "ActiveManyLine" : "StaticLine"
        },
        userIcon() {
            return (this.$route.path.indexOf('demarcate') != -1) ? "ActiveBanlance" : "StaticBanlance"
        },
    },
}
</script>
<style scoped>
a {
    text-decoration: none;
    color: var(--C65);
}

.debug-box {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;

}

.debug-nav-line {
    position: relative;
    width: 100%;
    height: 7.3438vh;
    display: flex;
    background-color: var(--Cee);
}

.debug-content {
    position: relative;
    flex: 1;
}

.route {
    flex: 6;
}

.icon-route {
    position: relative;
    top: 11%;
}

.route div {
    font-size: var(--font1_625);
    color: var(--C3B) !important;

}

.router-link-active {
    color: var(--C00c155);
    font-weight: 600;
}

.vertical-line {
    flex: 1;
    height: 4rem;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.line {
    display: block;
    height: 2rem;
    width: 2px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--Cc6);
}
</style>